.container {
    max-width: 120rem;
    margin: 8rem auto;
    background-color: $color-grey-light-1;
    box-shadow: $shadow-dark;

    min-height: 50rem;

    @media only screen and (max-width: $bp-largest) {
        margin: 0;
        max-width: 100%;
        width: 100%;

    }
}

.header {
    font-size: 1.4rem;
    height: 7rem;
    background-color: #fff;
    border-bottom: 1px solid $color-grey-light-1;

    display: flex;
    justify-content: space-between;
    align-items: center;

    @media only screen and (max-width: $bp-smallest) {
        flex-wrap: wrap;
        align-content: space-around;
        height: 11rem;
    }
}

.content {
    display: flex;


    @media only screen and (max-width: $bp-medium) {
        flex-direction: column;

    }
}

.sidebar {
    background-color: $color-grey-dark-1;

    flex: 0 0 18%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.jira-view {
    background-color: #fff;

    flex: 1;
}

.detail {
    font-size: 1.4rem;
    display: flex;
    padding: 4.5rem;
    background-color: $color-grey-light-1;
    border-bottom: $line;

    @media only screen and (max-width: $bp-medium) {
        padding: 3rem;

    }

    @media only screen and (max-width: $bp-small) {
        flex-direction: column;
    }
}

.description {
    background-color: #fff;
    box-shadow: $shadow-light;
    padding: 3rem;
    flex: 0 0 60%;
    margin-right: 4.5rem;

    @media only screen and (max-width: $bp-medium) {
        padding: 2rem;
        margin-right: 3rem;

    }

    @media only screen and (max-width: $bp-small) {
        margin-right: 0;
        margin-bottom: 3rem;
    }
}

.user-reviews {

    flex: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
}